import { color, distance, clamp, randomBetween, shuffle } from './utils/utils';
import colorArr from './colors.json';

export const cols = 20;
export const rows = 20;
export const colors = shuffle(colorArr.map(c => color(c[0], c[1], c[2]))).slice(0, cols * rows);

export function randomColor() {
  return Math.floor(randomBetween(0, colors.length));
}

export function getColor(x, y, points, radius) {
  let _colors = [];
  for (let i = 0; i < points.length; i++) {
    let perc = 1 - (distance(points[i].x, points[i].y, x, y) / radius);
    _colors.push(color(
      colors[points[i].color].r * perc,
      colors[points[i].color].g * perc,
      colors[points[i].color].b * perc,
    ));
  }

  let col = color(0, 0, 0);
  _colors.forEach(c => {
    col.r += c.r;
    col.g += c.g;
    col.b += c.b;
  });

  // col.r /= points.length;
  // col.g /= points.length;
  // col.b /= points.length;

  return color(
    clamp(col.r, 255, 0),
    clamp(col.g, 255, 0),
    clamp(col.b, 255, 0),
  );
}
