import { colorToString } from './utils/utils';
import { getcolor } from './color';

self.onmessage = function(e) {
  switch (e.data.msg) {
    case 'generate':
      let dpr = e.data.dpr;
      let canvas = e.data.canvas;
      let ctx = canvas.getContext('2d');

      canvas.width = (circleRadius * 2) * dpr;
      canvas.height = (circleRadius * 2) * dpr;

      ctx.scale(dpr, dpr);
      ctx.save();

      ctx.beginPath();
      ctx.moveTo(pathPoints[0].x, pathPoints[0].y);

      for (let i = 1; i < pathPoints.length; i++) {
        ctx.lineTo(pathPoints[i].x, pathPoints[i].y);
      }
      ctx.clip()

      let size = circleRadius * 2;
      // let radius = diagonal(size, size);
      let radius = circleRadius * 2;

      for (let x = 0; x < size; x++) {
        for (let y = 0; y < size; y++) {
          ctx.fillStyle = colorToString(getColor(x, y, colorPoints, radius));
          ctx.fillRect(x, y, 1, 1);
        }
      }

      ctx.restore();

      self.postMessage({ foo: 'bar' });
      break;
  }
};
